<template>
    <div class="center">
        <div class="top-bar">
            <ul class="bar-left left">
                <li><router-link to="/" tag="a">首页</router-link></li>
                <li><router-link to="/list" tag="a">菜谱</router-link></li>
            </ul>
            <div style="float:right"> 
                <img :src="topavatar" class="avatar" style="width:32px;height:32px;border-radius:50%">
                <router-link to="/center/my_settings_profile" tag="a">{{this.$store.state.user.username}}</router-link>
                <span>|</span>
                <router-link to="" @click.prevent="logout" tag="a">退出</router-link> 
            </div>
        </div>
        <div class="c-wrap">
            <div class="c-left">
                <div class="c-menu">
                    <ul>
                        <li>
                            <router-link to="/center/my_settings_profile" tag="a">账户设置</router-link>
                        </li>
                        <li>
                            <router-link to="/center/my_recipe_me" class="on" tag="a">菜谱</router-link>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="c-main">
                <div class="mod_location">
                    <router-link to="#" class="on" tag="a">发布新菜谱</router-link>
                    <!-- <a href="#">收货地址</a>
                    <a href="#">修改密码</a> -->
                </div>
                <div class="mod_wrap">
                    <!-- <form class="addmenuform" action=""> -->
                        <div>
                            <p>菜谱名称</p>
                            <input name="menuname" type="text" v-model="data.name" />
                        </div>
                        <div>
                            <p>菜谱分类</p>
                            <select id="sendSybol" v-model="data.categoryId">
                                <option  v-for="(item,index) in categories" :key="index" :value="item.id">{{item.name}}</option>
                            </select>

                        </div>
                        <div>
                            <p>成品图片（最多9张）</p>
                            <label class="filelabel">
                                <span>上传成品图</span>
                                <input name="addfile" type="file" multiple id="uploadImg" @change="handleUpload($event)" />
                            </label>
                            <ul class="thumb_list">
                                <li v-for="cover in data.covers" :key="cover.id">
                                    <img :src="`/public/uploads/cookbooks/${cover}`" alt="">
                                </li>
                            </ul>
                            <textarea name="describe" placeholder="请输入菜谱描述，最多输入500字" v-model="data.description"></textarea>
                        </div>
                        <div>
                            <p>基本参数</p>
                            <ul class="optionlist clearfix">
                                <li>
                                    <Single param-title="难度" :list='params.levels' @selecthandle='currentLevel'></Single>
                                </li>
                                <li>
                                    <Single param-title="口味" :list='params.tastes' @selecthandle='currentTeste'></Single>
                                </li>
                                <li>
                                    <Single param-title="工艺" :list='params.crafts' @selecthandle='currentCraft'></Single>
                                </li>
                                <li>
                                    <Single param-title="耗时" :list='params.needTimes' @selecthandle='currentTime'></Single>
                                </li>
                                <li>
                                    <Multiple param-title="厨具" :list='params.cookers' @selecthandle='currentCooker'></Multiple>
                                    
                                </li>
                            </ul>
                        </div>
                        <div>
                            <p>食物明细（新功能：支持添加多组食材，并可以修改食材组名）</p>
                            <div class="addfood">
                                <AddItem title="主料" :value='data.ingredients.m'></AddItem>
                                <AddItem title="辅料" :value='data.ingredients.s'></AddItem>
                            </div>
                        </div>
                        <Steps :value='data.steps'></Steps>
                        <div>
                            <p class="unrequired">小窍门</p>
                            <textarea class="tips" name="" placeholder="最多输入500字" v-model='data.tips'></textarea>
                        </div>
                        <button class="release" @click="postCookBooks">发布菜谱</button>
                    <!-- </form> -->
                </div>
            </div>
        </div>
        
    </div>
</template>
<script>
import '@/assets/css/user_form.css';
import '@/assets/css/user_publish.css';
import '@/assets/css/center_user.css';
import {postCookbookPicApi,postPublishApi} from '@/api/user';

import Single from '@/components/single.vue';
import Multiple from '@/components/multiple.vue';
import AddItem from '@/components/addItem.vue';
import Steps from '@/components/steps.vue';

export default {
    components:{
        Single,
        Multiple,
        AddItem,
        Steps
    },
    data(){
        return{
            topavatar : `http://localhost:8888/public/uploads/avatar/${this.$store.state.user.avatar}`,
            data : {
                name:'',
                categoryId:'',
                covers:[],
                description:'',
                level:'',
                taste:'',
                craft:'' ,
                cookers:[],
                ingredients:{
                    m:[],
                    s:[]
                },
                steps:[],
                tips:''  
            },
            params:{
                levels:['简单','普通','高级','神级'],
                tastes:['微辣','中辣','超辣','麻辣','酸辣','酸甜','酸咸','咸鲜','咸甜'],
                crafts:['烧','炒','爆','焖','炖','蒸','煮','拌','烤'],
                needTimes:['十分钟','廿分钟','半小时','三刻钟','一小时','数小时','一天','数天'],
                cookers:['炒锅','煮锅','平底锅','蒸锅','不粘锅','电烤箱','食物调理机','砂锅']           
            },
            
            
        }
    },
    computed:{
      categories(){
          return this.$store.state.category.category
      }
    },
    mounted:function(){
        //state里没有category 则触发store里的action 向后台请求数据
         this.$store.dispatch('asyncGetdata');
    },
    methods:{
        //上传成品图
        async handleUpload(e){
            // console.log(e.target.files);
            var files = [...e.target.files];
            files.forEach(async file=>{
                 let fd = new FormData();
                 fd.append('cover',file);
                 let resCookbookPic = await postCookbookPicApi(fd); 
                 if(resCookbookPic && !resCookbookPic.data.code){
                    // console.log(resCookbookPic.data.coverUrl);
                    this.data.covers.push(resCookbookPic.data.coverUrl);
                    // console.log(this.data.covers);
                }
            })
        },
        currentLevel(cur){
            this.data.level = cur;
        },
        currentTeste(cur){
            this.data.taste = cur;
        },
        currentCraft(cur){
            this.data.craft = cur;
        },
        currentTime(cur){
            this.data.needTime = cur;
        },
        currentCooker(cur){
            this.data.cookers = cur;
        },
        //发布新菜谱
        async postCookBooks(){
            let res = await postPublishApi(this.data);
        }

    }
}
</script>


